import React, { useState } from "react";
import { Modal, Button, Form, Col } from "react-bootstrap";
import TextareaAutosize from "react-textarea-autosize";
import styles from "../../styles/Styles.module.css";

const CreateModal = (props) => {
  const [show, setShow] = useState(false);
  const [firstName, setFirstName] = useState(localStorage.getItem("firstName"));
  const [lastName, setLastName] = useState(localStorage.getItem("lastName"));
  const [headline, setHeadline] = useState(localStorage.getItem("headline"));
  const [linkedinId, setLinkedinId] = useState(
    localStorage.getItem("linkedinId")
  );
  const [githubId, setGithubId] = useState(localStorage.getItem("githubId"));
  const [instagramId, setInstagramId] = useState(
    localStorage.getItem("instagramId")
  );
  const [facebookId, setFacebookId] = useState(
    localStorage.getItem("facebookId")
  );
  const [twitterId, setTwitterId] = useState(localStorage.getItem("twitterId"));
  const [mediumId, setMediumId] = useState(localStorage.getItem("mediumId"));
  const [description, setDescription] = useState(
    localStorage.getItem("description")
  );
  const [githubRepos, setGithubRepos] = useState(
    localStorage.getItem("githubRepos")
  );
  const [skills, setSkills] = useState(localStorage.getItem("skills"));

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleSave = () => {
    let saveObj = { firstName: firstName, lastName: lastName };
    localStorage.setItem("userData", JSON.stringify(saveObj));
    setShow(false);
    props.setIsChanged(true);
  };

  return (
    <>
      <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
        <li className="nav-item">
          <a className="nav-link lead" onClick={handleShow}>
            <b>{"<Make Your Own Portfolio />"}</b>
          </a>
        </li>
      </ul>
      <Modal
        show={show}
        onHide={handleClose}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className={styles.createModal}
      >
        <Modal.Header>
          <Modal.Title>Let's make your own portfolio!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Row>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={firstName}
                  placeholder={
                    localStorage.getItem("firstName") == null ||
                    localStorage.getItem("firstName") == ""
                      ? "First Name"
                      : localStorage.getItem("firstName")
                  }
                  onChange={(e) => {
                    localStorage.setItem("firstName", e.target.value);
                    setFirstName(e.target.value);
                  }}
                />
              </Col>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={lastName}
                  placeholder={
                    localStorage.getItem("lastName") == null ||
                    localStorage.getItem("lastName") == ""
                      ? "Last Name"
                      : localStorage.getItem("lastName")
                  }
                  onChange={(e) => {
                    localStorage.setItem("lastName", e.target.value);
                    setLastName(e.target.value);
                  }}
                />
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={headline}
                  placeholder={
                    localStorage.getItem("headline") == null ||
                    localStorage.getItem("headline") == ""
                      ? "Headline (A short and sweet bio goes a mile :))"
                      : localStorage.getItem("headline")
                  }
                  onChange={(e) => {
                    localStorage.setItem("headline", e.target.value);
                    setHeadline(e.target.value);
                  }}
                />
              </Col>
            </Form.Row>
          </Form>
          <br />
          <Form>
            <Form.Row>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={linkedinId}
                  placeholder={
                    localStorage.getItem("linkedinId") == null ||
                    localStorage.getItem("linkedinId") == ""
                      ? "LinkedIn ID"
                      : localStorage.getItem("linkedinId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("linkedinId", e.target.value);
                    setLinkedinId(e.target.value);
                  }}
                />
              </Col>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={githubId}
                  placeholder={
                    localStorage.getItem("githubId") == null ||
                    localStorage.getItem("githubId") == ""
                      ? "GitHub ID"
                      : localStorage.getItem("githubId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("githubId", e.target.value);
                    setGithubId(e.target.value);
                  }}
                />
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={instagramId}
                  placeholder={
                    localStorage.getItem("instagramId") == null ||
                    localStorage.getItem("instagramId") == ""
                      ? "Instagram ID (We'll use your profile picture here)"
                      : localStorage.getItem("instagramId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("instagramId", e.target.value);
                    setInstagramId(e.target.value);
                  }}
                />
              </Col>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={facebookId}
                  placeholder={
                    localStorage.getItem("facebookId") == null ||
                    localStorage.getItem("facebookId") == ""
                      ? "Facebook ID"
                      : localStorage.getItem("facebookId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("facebookId", e.target.value);
                    setFacebookId(e.target.value);
                  }}
                />
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={twitterId}
                  placeholder={
                    localStorage.getItem("twitterId") == null ||
                    localStorage.getItem("twitterId") == ""
                      ? "Twitter ID"
                      : localStorage.getItem("twitterId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("twitterId", e.target.value);
                    setTwitterId(e.target.value);
                  }}
                />
              </Col>
              <Col>
                <Form.Control
                  className={styles.textBoxStyle}
                  value={mediumId}
                  placeholder={
                    localStorage.getItem("mediumId") == null ||
                    localStorage.getItem("mediumId") == ""
                      ? "Medium ID"
                      : localStorage.getItem("mediumId")
                  }
                  onChange={(e) => {
                    localStorage.setItem("mediumId", e.target.value);
                    setMediumId(e.target.value);
                  }}
                />
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <TextareaAutosize
                  value={description}
                  placeholder={
                    localStorage.getItem("description") == null ||
                    localStorage.getItem("description") == ""
                      ? "About You (Write as much as you want :P)"
                      : localStorage.getItem("description")
                  }
                  onChange={(e) => {
                    localStorage.setItem("description", e.target.value);
                    setDescription(e.target.value);
                  }}
                  className={styles.aboutMeText}
                  minRows="4"
                ></TextareaAutosize>
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <TextareaAutosize
                  value={githubRepos}
                  placeholder={
                    localStorage.getItem("githubRepos") == null ||
                    localStorage.getItem("githubRepos") == ""
                      ? "Your comma separated GitHub repo names. (Keep the names exact as we'll extract these repos and display the relevant details in a pretty card format :))"
                      : localStorage.getItem("githubRepos")
                  }
                  onChange={(e) => {
                    localStorage.setItem("githubRepos", e.target.value);
                    setGithubRepos(e.target.value);
                  }}
                  className={styles.aboutMeText}
                  minRows="3"
                ></TextareaAutosize>
              </Col>
            </Form.Row>
            <br />
            <Form.Row>
              <Col>
                <TextareaAutosize
                  value={skills}
                  placeholder={
                    localStorage.getItem("skills") == null ||
                    localStorage.getItem("skills") == ""
                      ? "Your skills (comma separated))"
                      : localStorage.getItem("skills")
                  }
                  onChange={(e) => {
                    localStorage.setItem("skills", e.target.value);
                    setSkills(e.target.value);
                  }}
                  className={styles.aboutMeText}
                  minRows="3"
                ></TextareaAutosize>
              </Col>
            </Form.Row>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={() => handleSave()}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default CreateModal;
